<template>
  <section class="rightlist-wdgz">
    <h2>我的关注</h2>
    <div class="control">
      <div class="left">
        <p>批量删除</p>
        <select name="" id="">
          <option value="">全部关注</option>
          <option value="">互相关注</option>
          <option value="">按关注时间排序</option>
          <option value="">按粉丝数排序</option>
        </select>
      </div>
      <div class="right">
        <div class="all">
          <input type="text" placeholder="输入昵称">
          <div class="img"><img src="../assets/images/search.jpg" alt=""></div>
        </div>
        
      </div>
    </div>
    <ul class="list">
      <li v-for="li in lists">
        <div class="con">
          <div class="upbk" style="background-image:url(../assets/images/tx.png)"></div>
          <div class="conwb">
            <div class="img"><img :src="li.src" :alt="li.alt"></div>
            <h4>{{li.nickname}}</h4>
            <hr>
            <p>{{li.introduce}}</p>
            <a href="javascript:;">取消关注</a>
          </div>
        </div>
      </li>
      <div class="clear"></div>
    </ul>
    <div class="page">
      <span class="updown noclick">《 </span>
      <ul class="pagelist">
        <li class="active">1</li>
        <li>2</li>
        <div class="clear"></div>
      </ul>
      <span class="updown">》</span>
      <div class="all">共2页</div>

    </div>
  </section>
</template>
<script>
export default {
	name: 'ucfollow',
	data(){
		return{
			lists:[
				{
					src:require("../assets/images/tx.png"),
					alt:"头像",
					nickname:"咸鱼小妹",
					introduce:"三维影视做着视觉设计的人",
				},
				{
					src:require("../assets/images/tx.png"),
					alt:"头像",
					nickname:"咸鱼小妹",
					introduce:"三维影视做着视觉设计的人",
				},
				{
					src:require("../assets/images/tx.png"),
					alt:"头像",
					nickname:"咸鱼小妹",
					introduce:"三维影视做着视觉设计的人",
				},
				{
					src:require("../assets/images/tx.png"),
					alt:"头像",
					nickname:"咸鱼小妹",
					introduce:"三维影视做着视觉设计的人",
				},
				{
					src:require("../assets/images/tx.png"),
					alt:"头像",
					nickname:"咸鱼小妹",
					introduce:"三维影视做着视觉设计的人",
				},

				
				
			]
		}
	}
}
</script>
<style lang="scss" scoped>
	section.rightlist-wdgz{
		margin:20px 0 60px 14px;
		padding:0 27px;
		width:900px;
    float: left;
    background-color: #fff;
		box-shadow: 0px 2px 3px rgba(0,0,0,0.1);
		opacity: 0.93;
		border-radius: 5px;
		h2{
			font-size: 28px;
			color: #6c6c6c;
			margin:40px auto 30px auto;
		}
		.control{
			height:50px;
			border-bottom:1px solid #dcdcdc;
			.left{
				float: left;
				p{
					font-size: 14px;
					color: #6c6c6c;
					display: inline-block;
				}
				select{
					width:120px;
					height:30px;
					padding-left:5px;
					margin-left: 15px;
					font-size: 14px;
					color:#b7b7b7;
					border-radius: 5px;
					border:1px solid #e9e9e9;
				}

			}
			.right{
				float:right;
				.all{position: relative;
					.img{
						width:36px;
						height: 24px;
						position: absolute;
						right:5px;
						top:5px;
						img{
							width:100%;
							height: 100%;
						}
					}
					input{
						width:186px;
						height: 30px;
						border-radius: 5px;
						padding-left: 10px;
						position: absolute;
						right:0px;
						border:1px solid #e9e9e9;
					}
				}
				
			}
		}
		ul.list{padding-top:20px;
			li{    
        display: inline-block;
        margin: 10px 13px 10px 0;
				&:nth-child(4n){
					margin-right:0;
				}
				&:hover{
					.con{
						.conwb{
							a{
								color:#fff;
								background:#EA6457;
							}
						}
					}
				}
				.con{
					position: relative;
					width:210px;
					height:270px;
					border:1px solid #dcdcdc;
					background-color: #fff;
					border-radius:5px;
					.upbk{
						position: absolute;
						width:100%;
						height:98px;
						opacity: 0.2;
						background-size:100%;
						background-position:0 30%;
						background-repeat: no-repeat;
					}
					.conwb{
						position: absolute;
						top:19%;
						left:9px;
						text-align: center;
						.img{
							width:89px;
							height: 89px;
							margin:0 auto;
							border-radius: 50%;
							overflow: hidden;
							img{
								width:100%;
								height: 100%;
							}
						}
						h4{

							font-size: 14px;
							color: #191919;
							font-weight:bold;
							margin:15px auto;
							
						}
						hr{
							width:62px;
							height: 0px;
							border:1px solid #bbb;
							border-radius: 5px;
							
						}
						p{
							font-size:12px;
							color: #bfbfbf;
							margin:15px auto;
						}
						a{
							width:193px;
							height:28px;
							color:#a5a5a5;
							line-height: 28px;
							border-radius: 5px;
							display: inline-block;
							background: #ebebeb;
							
						}
					}
	
				}
			}
			
		}
		.page{
			float: right;
			margin: 30px auto 40px auto;
			span.updown{
				cursor: pointer;
				color:#535353;
				display: inline-block;
			}
			span.noclick{
				color: #cbcbcb;
			}
			ul.pagelist{margin:0 5px;
				display: inline-block;
				li{
					width:21px;
					height:21px;
          cursor: pointer;
          display: inline-block;
					margin:0 5px;
					text-align: center;
					line-height: 21px;
					color:#535353;
					font-size: 14px;
					&:hover{
						background-color: #EA6457;
						color: #fff;
						border-radius: 50%;
					}
					&.active{
						background-color: #EA6457;
						color: #fff;
						border-radius: 50%;
					}
				}
				
			}
			.all{
				margin-left: 10px;
				font-size:14px;
				color:#2b2b2b;
				display: inline-block;
			}
		}
	}
</style>



